<template lang="pug">
	.connected-user
		.connected-user__container
			.connected-user__items
				.connected-user__item
					.image-user.image-user__mini
						img(:src="currentUser.photoURL", alt="avatar")
					.content-user
						.content-user__header
							.nu-text {{ currentUser.displayName }}
					.extra
						button(@click='logout')
							icon(name="sign-out")
</template>

<script>
	import { mapGetters } from 'vuex'

	export default {
		name: 'connected-user',
		computed: {
			...mapGetters(['currentUser'])
		},
		methods: {
			logout() {
				firebase.auth().signOut()
				this.$store.dispatch('setUser', null)
				this.$router.push('/')
			}
		}
	}

</script>

<style lang="stylus" scoped>
	.image-user
		img
			width 60px
			border-radius 100%
	.image-user
		float left

	.content-user
		float left
		&__item:before
			content ""
			display table
			width 100%
			clear both
			height 0
	.extra
		float right


</style>



