<template lang="pug">
	.login__container
		.login__title
			h1  Sign up on chat room!
		form.login__form(:class="{ 'error' : hasErrors }")
			.login__field
				input.nu-input(type="text", name="name", placeholder="Name", v-model.trim='name', required)
			.login__field
				input.nu-input(type="email", name="email", placeholder="Email", v-model.trim='email', required)
			.login__field
				input.nu-input(type="password", name="password", placeholder="Password", v-model.trim='password', required)
			.login__field
				input.nu-input(type="password", name="password_conformation", placeholder="Password conformation", v-model.trim='password_confirmation', required)
			div
				.nu-button.nu-button__color(@click.prevent="register", :class="{ 'loading': isLoading }") Sign up
			div
				.nu-text.nu-text__error(v-if="hasErrors", v-for="error in errors") {{ error }}
		.login__message
			p - or -
			router-link(to='/login')
				.nu-button.nu-button__ok Log in
</template>

<script>
	import md5 from 'md5'
	export default {
		name: 'register',
		data () {
			return {
				name: '',
				email: '',
				password: '',
				password_confirmation: '',
				errors: [],
				usersRef: firebase.database().ref('users'),
				isLoading: false
			}
		},
		computed: {
			hasErrors (){
				return this.errors.length > 0
			}
		},
		methods: {
			register (){
				this.errors = [];
				if(this.isFormValid()) {
					this.isLoading = true
					firebase.auth().createUserWithEmailAndPassword(this.email, this.password).then( user => {

						user.updateProfile({
							displayName: this.name,
							photoURL: "http://www.gravatar.com/avatar/"+md5(user.email)+"?d=identicon"
						}).then ( () => {

							this.saveUserToUsersRef(user).then( () => {
								this.$store.dispatch('setUser', user)
								this.$router.push('/chat')
							})

						}, error => {
							console.log(error)
							this.errors.push(error.message)
							this.isLoading = false
						})

					}).catch( error => {
						console.log(error)
						this.errors.push(error.message)
						this.isLoading = false
					})
				}
			},
			saveUserToUsersRef(user){
				return this.usersRef.child(user.uid).set({
					name: user.displayName,
					avatar: user.photoURL
				})
			},
			isEmpty (){
				if(this.name.length == 0 || this.email.length == 0 || this.password.length == 0 || this.password_confirmation.length == 0){
					return true;
				}
				return false;
			},
			passwordValid () {
				if(this.password.length < 6 || this.password_confirmation.length < 6){
					return false;
				}
				if(this.password !== this.password_confirmation){
					return false;
				}
				return true;
			},
			isFormValid() {
				if(this.isEmpty()){
					this.errors.push('Please fill in all fields.')
					return false;
				}
				if(!this.passwordValid()){
					this.errors.push('Passwords do not match.')
					return false
				}
				return true
			}
		}
	}
</script>



