<template lang='pug'>
	.login__container
		.login__title
			h1 Log in on chat room!
		form.login__form(:class="{ 'error' : hasErrors }")
			.login__field
				input.nu-input(type="email", name="email", placeholder="Email", v-model.trim='email')
			.login__field
				input.nu-input(type="password", name="password", placeholder="Password", v-model.trim='password')
			div
				.nu-button.nu-button__color(@click.prevent="login", :class="{ 'loading': isLoading }") Log in
			div
				.nu-text.nu-text__error(v-if="hasErrors", v-for="error in errors") {{ error }}
		.login__message
			p - or -
			router-link(to='/register')
				.nu-button.nu-button__ok Sign up
</template>

<script>
	export default {
		name: "Login",
		data (){
			return {
				email: "",
				password: "",
				errors: [],
				isLoading: false
			}
		},
		computed: {
			hasErrors (){
				return this.errors.length > 0
			}
		},
		methods: {
			login (){
				console.log('login')
				this.errors = []

				if(this.isFormValid()){
					this.isLoading = true
					firebase.auth().signInWithEmailAndPassword(this.email, this.password).then(user => {
						this.$store.dispatch('setUser', user)
						this.$router.push('/chat')
					}).catch(error => {
						this.errors.push(error.message)
						this.isLoading  = false
					})
				}
			},
			isFormValid(){
				if(this.email.length > 0 && this.password.length > 0){
					return true;
				}
				return false;
			}
		}
	}
</script>



