<template>
	<ul class="nu-breadcrumbs" v-if="crumbs.length" style="">
		<li v-for="(crumb, i) in crumbs">
			<router-link v-if="i < crumbs.length-1" :to="crumb.path">
				{{ crumb.name }}
				<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" version="1.1" viewBox="0 0 129 129" enable-background="new 0 0 129 129" width="512px" height="512px">
					<g>
						<path d="m40.4,121.3c-0.8,0.8-1.8,1.2-2.9,1.2s-2.1-0.4-2.9-1.2c-1.6-1.6-1.6-4.2 0-5.8l51-51-51-51c-1.6-1.6-1.6-4.2 0-5.8 1.6-1.6 4.2-1.6 5.8,0l53.9,53.9c1.6,1.6 1.6,4.2 0,5.8l-53.9,53.9z" fill="#2c3e50"/>
					</g>
				</svg>
			</router-link>
			<span class=last v-else style="cursor:default">
				{{ crumb.name }}
			</span>
		</li>
	</ul>
</template>

<script>
	export default {
		name: 'nuBreadcrumbs',
		props: {
			root: String
		},
		computed: {
			crumbs: function()
			{
				var path = '', title = (this.root || 'home');
				var cs = [ { name: title, path: '/'} ]; if(!this.$route) return [];
				var r = (this.$route.path                        ).split('/');
				var m = (this.$route.matched[0].meta.crumbs || '').split('/');
				for(var i = 1; i < r.length; i++) {
					var name = (m[i] || r[i]); if(r[i] == '') continue;
					// title += ' : '+ name;
					path  += '/'  + name;
					cs.push({ name: name, path: path });
				}
				// window.document.title = title;
				return cs;
			}
		}
	}
</script>

<style>
	.nu-breadcrumbs {
		list-style: none;
		margin:0;
		padding:0;
		display: inline-block;
	}
	.nu-breadcrumbs li {
		display: inline-block;
		margin: 0 4px 0 0;
	}
	.nu-breadcrumbs li a {
		display: block;
    	line-height: 16px;
    	font-size: 16px;
    	text-decoration: none;
	}
	.nu-breadcrumbs li svg {
    	width: 16px;
    	height: 10px;
	}
</style>






