<template lang="pug">
	#app
		.navigation
			.logo
				router-link(to="/")
					icon(name="code")
			.navigation__ligin-container
				router-link(to='/register') Sign up
				router-link(to='/login') Log in
				connected-user
			ul.nu-menu
				li.nu-menu__link
					router-link(to='/') Home
				li.nu-menu__link
					router-link(to='/works') Works
				li.nu-menu__link
					router-link(to='/chat') Chat
				li.nu-menu__link
					router-link(to='/about') About
				li.nu-menu__link
					router-link(to='/contacts') Contacts
		nu-breadcrumbs
		router-view
		vue-progress-bar
		.footer
			.footer__block
				.nu-text
					p &copy; Copyright
				ul.social-icons
					li.social-icons__item
						a(href="https://vk.com/kushner.nikolai", target="_blank", rel="noopener")
							icon(name="vk")
					li.social-icons__item
						a(href="https://www.facebook.com/kushner.nikolai", target="_blank", rel="noopener")
							icon(name="facebook")
					li.social-icons__item
						a(href="https://www.instagram.com/nikolay_kushner", target="_blank", rel="noopener")
							icon(name="instagram")
					li.social-icons__item
						a(href="https://github.com/KushnerMikalai",target="_blank", rel="noopener")
							icon(name="github")
					li.social-icons__item
						a(href="https://www.npmjs.com/~nikolai_kushner", target="_blank", rel="noopener")
							icon(name="code-fork")
</template>
<script>
	import ConnectedUser from './components/sidebar/connectedUser'

	export default {
		name: 'app',
		components: { ConnectedUser },
		data() {
			return {
				logo: ''
			}},
		mounted () {
			this.$Progress.finish()
		},
		methods: {
			nu_open_modal: function () {
				this.$refs.modal.open()
			}
		},
		created () {
			this.$Progress.start();
			this.$router.beforeEach((to, from, next) => {
				if (to.meta.progress !== undefined) {
					let meta = to.meta.progress;
					this.$Progress.parseMeta(meta)
				}
				this.$Progress.start();
				next();
			});
			this.$router.afterEach((to, from) => {
				this.$Progress.finish()
			});
		}
	}
</script>

<style lang='stylus' src='./assets/style/app.styl'></style>



