import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import VueProgressBar from 'vue-progressbar'
import 'vue-awesome/icons'
import Icon from 'vue-awesome/components/Icon'
import NuModal from 'nu-modal-vue'
import NuBreadcrumbs from 'nu-breadcrumbs'

// pages components
import Home from './components/home'
import About from './components/about'
import Contacts from './components/contacts'
import Works from './components/works'
import Health24 from './components/works/24health'
import Login from './components/login'
import Register from './components/register'
import Chat from './components/chat'
import store from './store'

import firebase from 'firebase'
let config = {
    apiKey: "AIzaSyCTXEazRFkuhDQZJPXAyJ5mxCVpUOiKm7c",
    authDomain: "nikolaikushner-7a9cc.firebaseapp.com",
    databaseURL: "https://nikolaikushner-7a9cc.firebaseio.com",
    projectId: "nikolaikushner-7a9cc",
    storageBucket: "nikolaikushner-7a9cc.appspot.com",
    messagingSenderId: "633950206497"
};
firebase.initializeApp(config);
window.firebase = firebase

Vue.component('icon', Icon);
Vue.use(NuModal);
Vue.use(NuBreadcrumbs);

Vue.use(VueProgressBar, {
	color: '#42b983',
	failedColor: '#874b4b',
	thickness: '3px',
	transition: {
		speed: '0.5s',
		opacity: '0.2s',
		termination: 300
	}
});

const routes = [
	{ path: '/', component: Home },
	{ path: '/about', component: About },
	{ path: '/contacts', component: Contacts },
	{ path: '/works', component: Works },
	{ path: '/works/24health', component: Health24 },
	{ path: '/login', component: Login },
	{ path: '/register', component: Register },
	{ path: '/chat', component: Chat, beforeEnter: (to, from, next) => {
		if(!firebase.auth().currentUser){
			next('/login')
		} else {
			next()
		}
	}}
];

Vue.use(VueRouter);
const router = new VueRouter({
	routes,
	mode: 'history'
});

const unsuscribe = firebase.auth().onAuthStateChanged(user => {
	store.dispatch('setUser', user)

	new Vue({
		el: '#app',
		template: '<App/>',
		components: { App },
		router,
		store
	}).$mount('#app');

	unsuscribe()
})



