<template lang='pug'>
	.home
		.home__back-image
		.home__header
			h1.name {{ name }}
			h2.name-description {{ nameDescription }}
		section.about-me
			.about-me__items
				.about-me__line

				.about-me__item
					img.bg-svg-icon(src="../assets/images/ico-bg-index.svg", alt="")
					.about-me__section
						.avatar
							img(src="../assets/images/user-image.jpg")
							img.svg-avatar(src='../assets/images/ico-avatar.svg')
						.about-me__title
							span &mdash;&mdash;
							span About
						.about-me__description
							p.about-me__text {{ about }}
					.about-me__section
						.about-me__title
							span &mdash;&mdash;
							span Experience
						.about-me__description
							p.about-me__text {{ experience }}

				.about-me__item
					img.bg-svg-icon-2(src="../assets/images/ico-bg-index-2.svg", alt="")
					.about-me__section
						.about-me__title
							span &mdash;&mdash;
							span clients (past and present)
						.about-me__description
							p.about-me__text {{ clients }}
					.about-me__section
						.about-me__title
							span &mdash;&mdash;
							span skills
						.about-me__description
							p.about-me__text {{ skills }}
					.about-me__section
						.about-me__title
							span &mdash;&mdash;
							span study
						.about-me__description
							p.about-me__text {{ study }}
</template>

<script>
	export default {
	  name: 'home',
	  data () {
			return {
				name: 'Nikolai Kushner',
				nameDescription: 'frontent developer',
				about: "I'm Nikolai, an Belarusian guy living in Minks, where I work as a frontend developer",
				experience: "In the last year I worked for 'Clickmedia'",
				clients: "Internet marketing agency - 'ClickMedia', CZM Belarus, MNSE.ru, freelanse",
				skills: "JavaScript, Vue.js, Gulp, Git, Html5, Css3, Stylus, Responsive design, Photoshop",
				study: 'IT-school Hi Tech Park, TeachMeSkills'
			}
	  }
	}
</script>

<style src="../assets/style/_home.styl" lang="styl"></style>



